<template>
    <div id="wrapper">
        <div id="content-wrapper" style="background-color: white !important;">
            <div style="width: 100%;">
                <div>
                    <div class="mt-1">
                        <img :src="img" style="height: 90px; width: 183px;">
                        <span class="text-center" style="display: inline-block; width: 68%; font-size: 36px">
                            Ideal Power Co.
                        </span>
                    </div>
                    <hr class="mt-0">
                    <div class="pt-3 px-3" style="color: black; display: flex; justify-content: space-between;">
                        <span>{{ type | typeFilter }} Detail for month {{ month }}/{{ year }} {{ special_staff == 'true' ? ' (Special Staffs)' : '' }} </span>
                    </div>
                </div>
                <v-progress-linear indeterminate v-if="loading"></v-progress-linear>
                <div style="margin-top: 12px; padding-inline: 4px;" v-else>
                    <table class="table table-bordered  text-center" style="font-size: 12pt !important; color: black;">
                        <thead>
                            <tr>
                                <th class="text-start" style="width: 10%;">List ID</th>
                                <th class="text-start" style="width: 12%;">Work Date</th>
                                <th class="text-start" style="width: 12%;">{{ type | typeFilter }}</th>
                                <th class="text-start">{{ type | typeFilter }} Reason</th>
                            </tr>
                        </thead>
                        <tbody>
                            <template v-for="staff in staffs" >
                                <tr :key="'stf' + staff.st_id" >
                                    <td colspan="2" class="text-start py-2" style="font-weight: bold; background-color: #CFCFD4 !important;">Staff: {{ staff.staff_name }}</td>
                                    <td colspan="2" class="text-start py-2" style="font-weight: bold; background-color: #CFCFD4 !important;">Total: {{ (staff.total || 0).toLocaleString() }}</td>
                                </tr>
                                <template v-for="emp in all_employees.filter(e => e.st_id === staff.st_id)">
                                    <tr :key="'emp_row' + emp.emp_id">
                                        <td colspan="4" class="text-start py-2" style="background-color: rgba(81, 224, 81, 0.52) !important;">Employee: {{ emp.full_name }}</td>
                                    </tr>
                                    <template v-for="detail in all_emoloyee_detail.filter(d => d.emp_id === emp.emp_id)">
                                        <tr :key="'det_row' + detail.id">
                                            <td class="text-start py-2" style="background-color: white !important;">{{ detail.dsl_id }}</td>
                                            <td class="text-start py-2" style="background-color: white !important;">{{ new Date(detail.work_date).toISOString().split("T")[0] }}</td>
                                            <td class="text-start py-2" style="background-color: white !important;">{{ (detail[type] || 0).toLocaleString() }}</td>
                                            <td class="text-start py-2" style="background-color: white !important; padding-left: 20px;">{{ detail[type + '_reason'] }}</td>
                                        </tr>
                                    </template>
                                </template>
                            </template>
                        </tbody>
                    </table>

                    <div class="text-start px-3 para-total" style="background-color: rgba(68, 171, 231, 0.52) !important; font-size: 16pt; font-weight: bold; width: 100%; padding: 8px; margin-top: 12px;">
                        Total {{ type | typeFilter }}: {{ totalAmount.toLocaleString() }} IQD
                    </div>
                </div>

                <br><br>
                <div class="d-flex px-5 mt-9" style="color: black; justify-content: space-between;">
                    <!-- <div class=" justify-content-center text-center">
                        <span>
                            بەڕێوبەر
                        </span>
                        <br>
                        <span> {{ manager }} </span>
                    </div> -->
                    <div class="justify-content-center text-center">
                        <span>
                            ب.پڕۆژەکان
                        </span>
                        <br>
                        <span> محمد عمر محمد </span>
                        <br>
                        <span> {{ new Date().toISOString().split('T')[0] }} </span>
                        <br>
                        <span> {{ currentTime }} </span>
                    </div>
                    <div class=" justify-content-center text-center">
                        <span>
                            ب.دارایی
                        </span>
                        <br>
                        <span> {{ kargeri }} </span>
                    </div>
                    
                    <div class=" justify-content-center text-center">
                        <img :src="h_signature" width="180px" height="130px" class="only-print" style="margin-left: 38px; margin-top: -80px;" alt="">
                        <div class="mt-n13">
                            <span>
                                ژمێریار
                            </span>
                            <br>
                            <span> {{ accountant }} </span>
                            <br>
                            <span> {{ new Date().toISOString().split('T')[0] }} </span>
                            <br>
                            <span> {{ currentTime }} </span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    data() {
        return {
            img: require('../assets/Ideal power logo.png'),
            month: '',
            year: '',
            type: '',
            staffs: [],
            all_employees: [],
            all_emoloyee_detail: [],
            loading: false,
            manager: '',
            kargeri: '',
            accountant: '',
            currentTime: new Date().toLocaleTimeString([], {hour: '2-digit', minute: '2-digit'}),
            h_signature: require('../assets/h-signature.jpg'),
            special_staff: 'false'
        }
    },
    mounted() {
        this.month = this.$route.params.month
        this.year = this.$route.params.year
        this.type = this.$route.params.type
        this.special_staff = this.$route.params.special_staff || 'false'

        this.getData()
    },
    methods: {
        getData() {
            this.loading = true
            this.$http.get('/getNames').then(({data}) => {
                this.manager = data.manager;
                this.kargeri = data.kargeri;
                this.accountant = data.accountant;

                this.$http.get('/employee/getDetailedMonthDetail/' + this.month + '/' + this.year + '/' + this.type + '/' + this.special_staff).then(r => {
                    this.staffs = r.data.staffs
                    this.all_employees = r.data.all_employees
                    this.all_emoloyee_detail = r.data.all_employee_detail
                    this.loading = false
                    this.$nextTick(() => {
                        setTimeout(() => {
                            window.print()
                            window.close()
                        }, 300)
                    })
                })
            })
        }
    },
    computed: {
        totalAmount() {
            return this.staffs.reduce((acc, staff) => acc + (staff.total || 0), 0)
        }
    },
    filters: {
        typeFilter(value) {
            return {
                transport: 'Transport',
                fine: 'Fine',
                food: 'Food',
                expense: 'Expense',
                loan: 'Loan',
                accomodation: 'Accommodation'
            }[value]
        }
    }
}
</script>

